function rwpReCaptchaLoad() {
    var siteKey, id, rc;
    jQuery('.rwp-recaptcha').each(function(i){
        siteKey = jQuery(this).data('sitekey');
        id = jQuery(this).attr('id');
        rc = grecaptcha.render(id, {
          'sitekey' : siteKey
        });
        jQuery('#' + id).data('grcid', rc);
    });
}

